@font-face {
  font-family: "beaufortforlol";
  src: url("../assets/fonts/BeaufortforLOL-Regular.ttf");
}
.info__container {
  width: 80%;
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 80vh;
  border-radius: 20px;
  padding-left: 20px;
  padding-right: 20px;
  box-shadow: 10px 25px 50px 10px rgb(0 0 0 / 50%);
  background: rgba(0, 0, 0, 0.7);
  overflow: hidden;
}
.info__container .info__image {
  width: 25%;
  height: 90%;
  border-radius: 10px;
}
.info__image img {
  width: 100%;
  height: 100%;
  border-radius: 10px;
}
.info__container .info__detail {
  width: 72%;
  height: 95%;
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.info__detail .title {
  font-size: 3rem;
  font-family: "beaufortforlol", cursive;
  color: #b49158;
  text-transform: uppercase;
  text-shadow: 4px 4px #201c16;
}
.info__detail .skill__box {
  list-style: none;
  width: 90%;
  height: 20%;
  padding-top: 70px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
.skill__box .skill__item {
  width: 100px;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
}
.skill__item .dismo img {
  width: 90%;
  height: auto;
  border-radius: 50%;
  box-shadow: 10px 25px 50px 10px rgb(0 0 0 / 50%);
  border: 3px #7e643a solid;
}
.skill__item:hover > .dismo {
  display: none;
}
.skill__item:hover > .skill__title {
  display: flex;
  transform: scale(1.1);
  transition: all 2s ease-in-out;
}
.skill__item .passive {
  height: 100%;
  width: 100%;
  align-items: center;
  justify-content: center;
  display: flex;
  flex-direction: column;
  font-size: 10px;
}
.skill__item .passive img {
  width: 60%;
  height: auto;
  border-radius: 2px;
  margin-bottom: 10px;
}
.skill__item .skill__title {
  width: 70%;
  height: 60%;
  font-size: 25px;
  font-weight: bold;
  border-radius: 50%;
  display: none;
  color: #ddd8cf;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  border: 2px #aa8f63 solid;
  text-shadow: 2px 2px #46361e;
  box-shadow: 10px 25px 50px 10px rgb(0 0 0 / 50%);
}
.skill__item .skill__title::before {
  content: "";
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  border-radius: 50%;
  background-color: rgba(0, 0, 0, 0.5);
}
.arrow__skins {
  font-family: "beaufortforlol", cursive;
  cursor: pointer;
  color: #7e643a;
}
.info__detail .lore__box {
  width: 90%;

  font-family: "beaufortforlol", cursive;
}
.lore__box .title {
  font-size: 1.5rem;
  color: #fff;

  text-shadow: 2px 2px #46361e;
}
.lore__box .content {
  font-family: "roboto", cursive;
  font-size: 1.1rem;
  line-height: 1.8rem;
  color: #ac9268;
}
@media screen and (max-width: 767px) {
  .info__container {
    overflow-x: scroll;
    height: 50vh;
    align-items: flex-start;
    padding-top: 20px;
  }
  .info__container .info__image {
    height: 40%;
  }
}
